.testimoniales {
  background-color: $verde;
  font-size: 2.4rem;
  color: $blanco;
  padding: 2rem;
  border-radius: 2rem;

  blockquote {
    padding-left: 5rem;
    position: relative;

    &::before {
      content: '';
      background-image: url('../img/comilla.svg');
        background-repeat: no-repeat;
      width: 6rem;
        height: 6rem;
        display: block;
      position: absolute;
      left: -2rem;
    }
  }

  p {
    color: $blanco;
    text-align: right;
  }
}

